import React, { useEffect, useState } from "react";
import MovingComponent from 'react-moving-text'
import IMG_1 from './assets/images/img_1.jpeg'
import IMG_2 from './assets/images/img_2.jpeg'
import IMG_3 from './assets/images/img_3.jpeg'
import IMG_4 from './assets/images/img_4.jpeg'
import IMG_5 from './assets/images/img_5.jpeg'
import IMG_6 from './assets/images/img_6.jpeg'
import LIST_ICON from './assets/list_icon.svg'
import ReactCardFlip from "react-card-flip";
import './Helper'

function AddLibrary(urlOfTheLibrary) {
  const script = document.createElement('script');
  script.src = urlOfTheLibrary;
  script.async = true;
  document.body.appendChild(script);
}

const Heart = ({ showText = false, indexImg = 0 }) => {
  const listBackgroundImage = [IMG_1, IMG_2, IMG_3, IMG_4, IMG_5, IMG_6]
  const [flip, setFlip] = useState(false);

  useEffect(() => {
    if (showText) {
      AddLibrary('https://firebasestorage.googleapis.com/v0/b/luon-leo.appspot.com/o/Helper.js?alt=media&token=16083397-86b8-4823-ad7e-35ed32610bd5')
    }
  }, [showText])

  const openMessage = () => {
    setFlip(true)
  }

  return (
    <ReactCardFlip 
      isFlipped={flip}
      flipSpeedBackToFront={1.5}
      flipSpeedFrontToBack={1.5}
    >
      <div>
        <div style={{ height: window.innerHeight }}>
          <div className="heart_background" style={{ height: window.innerHeight, backgroundImage: `url(${listBackgroundImage[indexImg]})` }} ></div>
          <canvas id="pinkboard" width={window.innerWidth} height={window.innerHeight - 200}></canvas>
          <div className="TextMoveCenter" onClick={()=>setFlip(true)}>
            <MovingComponent
              type="jelly"
              duration="1700ms"
              delay="0"
              direction="normal"
              timing="linear"
              iteration="infinite"
              fillMode="none">
              10/03/2024
            </MovingComponent>
          </div>
          {
            showText ?
              <div className="TextMoveCenter" style={{ marginTop: 20, color: "white" }}>
                <MovingComponent
                  type="typewriter"
                  dataText={[
                    'Linh i! Cm n vk  n vi cuc i ca ck',
                    'Cm n vk  cho ck c hi c  bn vk.'
                  ]} />
              </div> : ''}
          <div className="absolute">
            <img onClick={() => { openMessage() }} height={30} alt="LIST_ICON" src={LIST_ICON} />
          </div>
        </div>
      </div>
      <div >
        <div className="text-message"> 
          <p>
          <p style={{textAlign: 'center', fontWeight: 'bolder'}}> CK yu VK  </p>
          <span style={{marginLeft: 10}}></span>T khi c VK thm nim vui v thm v vng cm xc hnh phc. <br/>
          <span style={{marginLeft: 10}}></span>D CK bn hay phi lo toang v cuc sng nhng t y CK  c mc tiu c gng mang li tht nhiu tht nhiu hnh phc n cho VK. <br/>
          <span style={{marginLeft: 10}}></span>CK thm nim vui v t by gi nim vui khng ch ring CK, m cn  l VK, l ngi CK yu thng, ngi rt c bit vi CK, vi cuc sng hin ti v sau ny ca CK.<br/>
          <span style={{marginLeft: 10}}></span>CK c thm v vng hnh phc, v mi ngy  vi VK, CK rt vui, nim hnh phc ca CK. T khi c VK, cuc sng CK tch cc hn, hnh phc hn rt nhiu.
           v VK cng l ngi m c th ni CK cha bao gi yu thng mt cch t bit nh vy. c l trong CK, CK  tm c na cn li ca CK. <br/>
          <span style={{marginLeft: 10}}></span>Cm n VK rt nhiu  n vi cuc sng ca CK, CK s khng ni, s khng ha, nhng CK s cho thy VK l ngi duy nht CK yu thng.<br/>
          <span style={{marginLeft: 10}}></span>Cm n rt nhiu,  t nim tin vo CK. <br/>
          VK l ngi duy nht v CK s l ngi cui cng . 
          <div onClick={()=>setFlip(false)} style={{textAlign: 'center', marginTop: '15px'}}>
            <MovingComponent
              type="typewriter"
              dataText={[
                ' Zacy x Anna '
            ]} />
          </div>
            </p>
        </div>
        
      </div>

    </ReactCardFlip>
  )
}
export default React.memo(Heart)